.Button {
  background-color: #e8e8e8;
  border-radius: 5px;
  border-style: solid;
  border-width: thin;
  font-size: 20px;
  padding: 10px 0;
}

.Button:nth-child(even) {
  background-color: #d3d3d3;
  color: #000;
}

.Button-big {
  grid-row-end: span 2;
}



